            <span class="left-normal-margin">状态</span>
            <el-select v-model="virtualStatusValue" placeholder="请选择" style="width: 120px">
                <el-option
                        v-for="item in virtualStatus"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
