                    label="状态"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="币类型"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="创建时间">
            </el-table-column>
        </el-table>
    </div>
