                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }],
                multipleSelection: []
            }
        },
        methods: {
            // 查询
            query() {

            },
            // 勾选变化 钩子
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>


